<template>
  <div class="servers-container">
    <div class="page-header">
      <h1></h1>
      <el-button type="primary" @click="showAddServerDialog"></el-button>
    </div>

    <el-table
      v-loading="loading"
      :data="servers"
      border
      style="width: 100%"
    >
      <el-table-column
        prop="name"
        label=""
        width="180"
      ></el-table-column>
      <el-table-column
        prop="host"
        label=""
        width="180"
      ></el-table-column>
      <el-table-column
        prop="port"
        label="SSH"
        width="100"
      ></el-table-column>
      <el-table-column
        prop="username"
        label=""
        width="120"
      ></el-table-column>
      <el-table-column
        prop="status"
        label=""
        width="100"
      >
        <template slot-scope="scope">
          <el-tag
            :type="getStatusTagType(scope.row.status)"
          >
            {{ statusText[scope.row.status] }}
          </el-tag>
          <el-tooltip content="" placement="top">
            <el-button 
              type="text" 
              icon="el-icon-refresh" 
              circle 
              size="mini" 
              @click="checkServerStatus(scope.row)"
            ></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column
        label=""
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.row)"
          ></el-button>
          <el-button
            v-if="scope.row.status !== 'online' && scope.row.status !== 'connecting' && scope.row.status !== 'disconnecting'"
            size="mini"
            type="success"
            @click="handleConnect(scope.row)"
          ></el-button>
          <el-button
            v-else-if="scope.row.status === 'online'"
            size="mini"
            type="warning"
            @click="handleDisconnect(scope.row)"
            :loading="disconnectingServers[scope.row._id]"
          ></el-button>
          <el-button
            v-else
            size="mini"
            disabled
          >{{ statusText[scope.row.status] }}</el-button>
          <el-button
            v-if="scope.row.status === 'online'"
            size="mini"
            type="primary"
            @click="handleManageRules(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- / -->
    <el-dialog
      :title="isEdit ? '' : ''"
      :visible.sync="dialogVisible"
      width="50%"
    >
      <server-form
        :is-edit="isEdit"
        :server-data="currentServer"
        @submit="handleFormSubmit"
      ></server-form>
    </el-dialog>
  </div>
</template>

<script>
import ServerForm from '@/components/ServerForm.vue';
import { mapActions } from 'vuex';

export default {
  name: 'ServersView',
  components: {
    ServerForm
  },
  data() {
    return {
      loading: false,
      servers: [],
      dialogVisible: false,
      isEdit: false,
      currentServer: null,
      statusText: {
        'online': '',
        'offline': '',
        'error': '',
        'connecting': '',
        'disconnecting': ''
      },
      disconnectingServers: {},
      statusCheckInterval: null
    };
  },
  created() {
    this.fetchServers();
    // 30
    this.statusCheckInterval = setInterval(() => {
      this.checkAllServersStatus();
    }, 30000);
  },
  beforeDestroy() {
    // 
    if (this.statusCheckInterval) {
      clearInterval(this.statusCheckInterval);
    }
  },
  methods: {
    ...mapActions('servers', [
      'getAllServers',
      'createServer',
      'updateServer',
      'deleteServer',
      'connectServer',
      'disconnectServer',
      'checkStatus'
    ]),
    async fetchServers() {
      this.loading = true;
      try {
        const response = await this.getAllServers();
        this.servers = response.data;
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    showAddServerDialog() {
      this.isEdit = false;
      this.currentServer = null;
      this.dialogVisible = true;
    },
    handleEdit(server) {
      this.isEdit = true;
      this.currentServer = { ...server };
      this.dialogVisible = true;
    },
    async handleFormSubmit(formData) {
      try {
        if (this.isEdit) {
          await this.updateServer({
            id: this.currentServer._id,
            data: formData
          });
          this.$message.success('');
        } else {
          await this.createServer(formData);
          this.$message.success('');
        }
        this.dialogVisible = false;
        this.fetchServers();
      } catch (error) {
        this.$message.error(error.message);
      }
    },
    async handleDelete(server) {
      try {
        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        
        await this.deleteServer(server._id);
        this.$message.success('');
        this.fetchServers();
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error(': ' + error.message);
        }
      }
    },
    async handleConnect(server) {
      try {
        this.loading = true;
        
        // ""
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          this.$set(this.servers[index], 'status', 'connecting');
        }
        
        // 
        await this.connectServer(server._id);
        this.$message.success('');
        
        // 
        await this.fetchServers();
      } catch (error) {
        this.$message.error(': ' + error.message);
        // 
        await this.checkServerStatus(server);
      } finally {
        this.loading = false;
      }
    },
    async handleDisconnect(server) {
      try {
        // 
        this.$set(this.disconnectingServers, server._id, true);
        
        // ""
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          this.$set(this.servers[index], 'status', 'disconnecting');
        }
        
        // 
        await this.disconnectServer(server._id);
        this.$message.success('');
        
        // 
        if (index !== -1) {
          this.$set(this.servers[index], 'status', 'offline');
        }
        
        // 
        await this.fetchServers();
      } catch (error) {
        this.$message.error(': ' + error.message);
        // 
        await this.checkServerStatus(server);
      } finally {
        // 
        this.$set(this.disconnectingServers, server._id, false);
      }
    },
    handleManageRules(server) {
      this.$router.push({ name: 'rules', params: { serverId: server._id } });
    },
    async checkServerStatus(server) {
      try {
        const response = await this.checkStatus(server._id);
        // 
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          this.$set(this.servers[index], 'status', response.data.data.status);
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    async checkAllServersStatus() {
      for (const server of this.servers) {
        await this.checkServerStatus(server);
      }
    },
    getStatusTagType(status) {
      switch (status) {
        case 'online':
          return 'success';
        case 'error':
          return 'danger';
        case 'connecting':
          return 'info';
        case 'disconnecting':
          return 'warning';
        default:
          return '';
      }
    }
  }
};
</script>

<style scoped>
.servers-container {
  padding: 20px;
}
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
</style> 